.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-container {
  display: grid;
  justify-content: center;
  margin: 10px auto;
  width: 1000px;
}

.button-container {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  margin-left:  300px;
  margin-right: 300px;
}

.button-container button {
  margin: 30px 100px;
}

.pokemon-item {
  border: black 1px solid;
  border-radius: 20px;
  margin: 5px;
}
.header-container
{
  text-align: center;
  display: grid;
  grid-template-columns: 5fr 1fr;
}
.filterButton
{
  margin-bottom: 10px;
}
